input[type="checkbox"] {
  display: none;
}

input[type="checkbox"] + label {
  font-family: "Axis";
  font-size: 24px;
  font-size: 2.4rem;
  font-weight: bold;
  color: #781738;
  padding-left: 40px;
  letter-spacing: -2px;
}

@media screen and (max-width: 768px) {
  input[type="checkbox"] + label {
    font-size: 20px;
    font-size: 2rem;
    padding-left: 30px;
  }
}

input[type="checkbox"] + label span {
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: #fff;
  border: 2px solid #999999;
  position: absolute;
  left: 0;
}

@media screen and (max-width: 990px) {
  input[type="checkbox"] + label span {
    top: 2px;
  }
}

@media screen and (max-width: 768px) {
  input[type="checkbox"] + label span {
    width: 20px;
    height: 20px;
    top: 4px;
  }
}

input[type="checkbox"]:checked + label span:before {
  content: '';
  background: url("../img/page01/ico-check.png") no-repeat center;
  background-size: 100% 100%;
  display: block;
  width: 35px;
  height: 27px;
  position: absolute;
  top: -5px;
  left: 0px;
}

@media screen and (max-width: 768px) {
  input[type="checkbox"]:checked + label span:before {
    width: 28px;
    height: 20px;
  }
}
